* {
  box-sizing: border-box; }

html, body {
  width: 100%;
  height: 100%; }

body {
  font-size: 16px;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  color: #2f3234; }

.container {
  margin: 0 auto;
  width: 940px; }

.wrapper {
  overflow: hidden;
  min-height: 100%;
  min-width: 960px; }

.section {
  height: 100vh;
  min-height: 650px; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-weight: normal; }

ul {
  padding: 0;
  margin: 0; }
  ul li {
    list-style: none; }

p {
  padding: 0;
  margin: 0; }

footer {
  min-width: 960px; }
